.search-result {
  display: flex;
  flex-direction: column;
  padding: 0 0 35px;
  border-bottom: 1.5px solid #fcbe13;

  &__title {
    color: #282f30;
    font-family: Manrope;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%; /* 24px */
    margin: 0 0 15px;
    padding: 0;
  }

  &__description {
    color: #282f30;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%; /* 20.8px */
    width: 855px;
    margin: 0;
    padding: 0 0;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
